.home-about-us {
   width: 100%;
   height: 100%;
   background-color: var(--main-white-color);
}
.home-about-us-grid {
   display: grid;
   grid-template-columns: repeat(12, 1fr);
   grid-template-rows: repeat(12, 50px);
   max-width: 1160px;
   margin: 0 auto;
   padding: 5em 0;
}

.about-us-img {
   width: 100%;
   height: auto;
   grid-row: 3/12;
   grid-column: 1/7;
   z-index: 20;
}
.about-us-section {
   margin: 2em 0 1em 0;
   grid-row: 1/13;
   background-color: var(--dark-accent-color);
   grid-column: 4/13;
   display: grid;
   grid-template-columns: repeat(12, 1fr);
}
.about-us-section h1 {
   grid-column: 6/12;
   margin: 2em 0 0 0;
   color: var(--main-white-color);
}
.about-us-section ol {
   grid-column: 6/12;
   margin-top: 2em;
   color: var(--main-white-color);
   line-height: 1.4;
}
.about-us-section button {
   grid-column: 10/12;
   width: 140px;
   height: 40px;
   font-size: 17px;
   font-weight: 700;
   color: var(--main-accent-color);
   background-color: var(--main-white-color);
   border: 1px solid var(--dark-accent-color);
}
.about-us-section button:hover {
   cursor: pointer;
   background-color: var(--dark-accent-color);
   border: 1px solid var(--main-white-color);
   color: var(--main-white-color);
}
